.wp-block-harvestworks-event-artists-list {

    display: flex;
    flex-direction: column;
    gap: 2.4rem;

    .artist_card {
        display: flex;
        gap: 1.8rem;

        background-color: var(--wp--preset--color--custom-material-3);
        padding: 2.4rem;
        align-items: stretch;
        width: auto;

        transition: background-color 0.3s;
        text-decoration: none !important;

        &:hover {
            background-color: lightgray;
            text-decoration: none !important;
        }

        img {
            object-fit: cover;
            height: 16rem;
            aspect-ratio: 1;
        }

        .artist_details {
            display: flex;
            flex-direction: column;
            padding: 0.6rem;
            margin-top: -1.2rem;


            .artist_name {
                margin: 0;
                margin-bottom: 1.2rem;
                margin-top: 0.6rem;
                line-height: 1;
                font-size: 3.2rem;
                font-weight: 500;
            }

            .artist_bio {
                margin: 0;
                margin-left: 0.2rem;
            }
        }
    }
}